import React from 'react';
import SuperButton from '../h4/common/c2-SuperButton/SuperButton';
import {useDispatch, useSelector} from 'react-redux';
import {AppStoreType} from './bll/store';
import {loadingAC} from './bll/loadingReducer';
import './Styles.css'



function HW10() {

    let dispatch = useDispatch()

    const loading = useSelector<AppStoreType>(state => state.loading)

    const setLoading = () => {
        dispatch(loadingAC(true))

        setTimeout(() => {
            dispatch(loadingAC(false))
        }, 2000)
        console.log('loading...');
    };



    return (
        <div>
            <hr/>
            homeworks 10

            {/*should work (должно работать)*/}
            {loading
                ? (
                    <div className="cssload-thecube">
                        <div className="cssload-cube cssload-c1"/>
                        <div className="cssload-cube cssload-c2"/>
                        <div className="cssload-cube cssload-c4"/>
                        <div className="cssload-cube cssload-c3"/>
                    </div>
                ) : (
                    <div>
                        <SuperButton onClick={setLoading}>set loading...</SuperButton>
                    </div>
                )
            }

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<Alternative/>*/}
            <hr/>
        </div>
    );
}

export default HW10;
